import React, { Component } from 'react';
import Container from '../container';
import NavBar from '../navbar';
import Header from '../header';
import Footer from '../footer';

class MainContainer extends Component {
    state = {
        score: 0,
        topScore: 0,
        msg:{ txt: "Play",class:"Normal"},
        items: {},
        images: [
            {src:'/assets/images/1.jpeg',name:'1'},
            {src:'/assets/images/2.jpeg',name:'2'},
            {src:'/assets/images/3.jpeg',name:'3'},
            {src:'/assets/images/4.jpeg',name:'4'},
            {src:'/assets/images/5.jpeg',name:'5'},
            {src:'/assets/images/6.jpeg',name:'6'},
            {src:'/assets/images/7.jpeg',name:'7'},
            {src:'/assets/images/8.jpeg',name:'8'},
            {src:'/assets/images/9.jpeg',name:'9'},
            {src:'/assets/images/10.jpeg',name:'10'},
            {src:'/assets/images/11.jpeg',name:'11'},
            {src:'/assets/images/12.jpeg',name:'12'}
        ]
    }
    re_sort_images = images => {
        let len = images.length;
        let tempArr = [];
        for ( let i = 0; i < len-1; i++ ) {
            tempArr.push(images.splice(Math.floor(Math.random()*images.length),1)[0]);
        }
        tempArr.push(images[0]);
        images = tempArr;
        return images;
    }
    handleClickEvent = query =>{
        let items = this.state.items;
        if (this.state.items[query]) {
            this.setState({
                msg: {txt: "You guessed incorrectly",class:"black" },
                score: 0,
                items: {},
                images: this.re_sort_images(this.state.images)
            });
        }else if (this.state.score === 12) {
            this.setState({
                msg: {txt: "You Won",class:"green" },
                score: 0,
                topScore:0,
                items: {},
                images: this.re_sort_images(this.state.images)
            });
        }else {
            items[query] = 1;
            this.setState({
                score: this.state.score + 1,
                topScore: this.state.topScore + 1,
                msg: {txt: "you guessed correctly",class:"green" },
                items: items,
                images:this.re_sort_images(this.state.images)
            });
        }

    }
    render() {
        return (
            <div>
                <NavBar
                    score={this.state.score}
                    topScore={this.state.topScore}
                    msg={this.state.msg}
                />
                <Header />
                <main className="container">
                    {
                        this.state.images.map(image => (
                            <Container
                                src={image.src}
                                name={image.name}
                                key = {image.id}
                                handleClickEvent = { this.handleClickEvent }
                            />
                          ))
                     }
                </main>
                <Footer/>
            </div>
        )
    }
}
export default MainContainer;